@import "utilities/variables";
@import "utilities/mixins";

body {
  background: map-get($colours, background);
}

.hero-header {
    margin: 0;
    height: 100vh;

    @include centerScaleBackground("/assets/images/engineering-plans.jpg");

    /* Keep background fixed */
    background-attachment: fixed;

    .hero-title-container {
        @include flexbox-container(column, center, center);
        height: 100%;
        width: 100%;
        position: relative;
        background: inherit;

        #hero-title {
            font-family: 'Dax', sans-serif;
            font-weight: 400;
            margin: 0;
            width: 75vw;
            font-size: 7.5vmin;
            text-align: left;
            padding: 10px;
            position: fixed;
            display: block;
        
            color: white;
        }
    }
}

.sticky-parent {
    width: 100%;
}

.about-container {
    height: 110vh;
    @include flexbox-container(row, flex-start, center);
    background-color: white;

    @include centerScaleBackground("/assets/images/partial-engine.jpg");

    width: 100%;
    box-sizing: border-box;
    box-shadow: inset 55vw 0 5vw -5vw #FFF;

    z-index: 100;

    position: sticky;
    top: 0;

    /* About actual content */
    #about-text {
        flex: 1 0 50vw;
        max-width: 50vw;
        padding: 6vw 8vw;

        h1 {
            font-family: 'Dax', sans-serif;
            font-weight: 400;
            font-size: 4.5vmin;
            padding: 20px;
            margin: 0;

            @media screen and (max-width: $side-change) {
                font-size: 3.75vh;
            }
        }
        
        h1:before, h1:after {
            content: '"';
        }

        /* Title */
        h2 {
            font-family: 'Roboto', sans-serif;
            margin: auto;
            padding: 20px;
        
            /* Underline after */
            &:after {
                background: none repeat scroll 0 0 black;
                bottom: -6px;
                content: "";
                display: block;
                height: 3px;
                position: relative;
                width: 100px;
            }
        }
        
        /* Paragraph */
        p {
            font-family: 'RobotoCondensed', sans-serif;
            font-weight: 400;
            margin: auto;
            padding: 0px 20px;
        }

        @media screen and (max-width: $side-change) {
            flex: 1 0 90vw;
            max-width: 90vw;
            padding: 10vw;
            background: white;
        }
    }
}

.skills-container {
    height: 110vh;
    @include flexbox-container(row, flex-end, center);
    background-color: #111;

    @include centerScaleBackground("/assets/images/turbocharger-inside.jpg");

    width: 100%;
    box-sizing: border-box;
    box-shadow: inset -45vw 0 5vw -5vw #111;

    z-index: 100;

    /* temporary fix for scroll bar*/
    overflow-x: hidden;

    position: sticky;
    top: 0;

    /* About actual content */
    #skills-text {
        flex: 1 0 50vw;
        max-width: 50vw;
        padding: 6vw 8vw;
        
        h1 {
            font-family: 'Dax', sans-serif;
            font-weight: 400;
            font-size: 4.5vmin;
            padding: 20px;
            margin: auto 0;
            color: white;
            height: 15vmin;

            @media screen and (max-width: 900px) {
                height: 20vmin;
            }

            @media screen and (max-width: $side-change) {
                height: 11.5vh;
                font-size: 3.75vh;
            }
        }

        /* Title */
        h2 {
            font-family: 'Roboto', sans-serif;
            margin: auto;
            padding: 20px;
            color: white;
        
            /* Underline after */
            &:after {
                background: none repeat scroll 0 0 white;
                bottom: -6px;
                content: "";
                display: block;
                height: 3px;
                position: relative;
                width: 100px;
            }
        }

        /* Paragraph */
        p {
            font-family: 'RobotoCondensed', sans-serif;
            font-weight: 400;
            margin: auto;
            padding: 0px 20px;
            color: white;
        }

        @media screen and (max-width: $side-change) {
            flex: 1 0 90vw;
            max-width: 90vw;
            padding: 10vw;
            background: #111;
        }
    }
}

/* Education timeline */
#timeline-section {
    background: map-get($colours, background);
    padding-bottom: 25px;
  }
  
  /* The actual timeline (the vertical ruler) */
  .timeline {
    position: relative;
    max-width: $total-width;
    margin: 0 auto;
    z-index: 10;
  }
  
  /* The actual timeline (the vertical ruler) */
  .timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: map-get($colours, nav);
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
  }
  
  /* Container around content */
  .container {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
  }
  
  /* The circles on the timeline */
  .container::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: white;
    border: 4px solid #FF9F55;
    top: 25px;
    border-radius: 50%;
    z-index: 1;
  }
  
  /* Place the container to the left */
  .left {
    left: 0;
    z-index: 10;
  }
  
  /* Place the container to the right */
  .right {
    left: 50%;
    z-index: 10;
  }
  
  /* Add arrows to the left container (pointing right) */
  .left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 32px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid white;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent white;
  }
  
  /* Add arrows to the right container (pointing left) */
  .right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 32px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }
  
  /* Fix the circle for containers on the right side */
  .right::after {
    left: -16px;
  }
  
  /* The actual content */
  .content {
    padding: 20px 30px;
    background-color: white;
    position: relative;
    border-radius: 6px;
  }
  
  /* Media queries - Responsive timeline on screens */
  @media screen and (max-width: $timeline-change) {
  /* Place the timelime to the left */
    .timeline::after {
      left: 31px;
    }
  
  /* Full-width containers */
    .container {
      width: 100%;
      padding-left: 70px;
      padding-right: 25px;
    }
  
  /* Make sure that all arrows are pointing leftwards */
    .container::before {
      left: 60px;
      border: medium solid white;
      border-width: 10px 10px 10px 0;
      border-color: transparent white transparent transparent;
    }
  
  /* Make sure all circles are at the same spot */
    .left::after, .right::after {
      left: 15px;
    }
  
  /* Make all right containers behave like the left ones */
    .right {
      left: 0%;
    }
  }